<script>
export default {
    name:"movies",
    data() {
        return {
            ids:null,
            detailMovie:null,
            img:[]
        }
    },
    created(){
        this.ids = this.$route.query.plan;
        this.$axios
      .get(
        "http://api.kele8.cn/agent/http://m.maoyan.com/ajax/detailmovie?",{
             params: {
          movieId: this.ids
        }
        }
      )
      .then(result => {
        console.log(result.data);
        this.detailMovie = result.data.detailMovie;
        this.detailMovie.img = "http://p0.meituan.net/177.249/movie/" + this.detailMovie.img.split("/")[5];
         this.detailMovie.photos.forEach(element => {
            element = "http://p0.meituan.net/movie/"+ element.split("/")[5] + "@250w_140h_1e_1c.webp";
            this.img.push(element);
        });
     this.img[0]  = 'http://p1.meituan.net/movie/ad22ec511a5222fda792e90766ab02bc406744.jpg@250w_140h_1e_1c.webp'
      console.log(this.detailMovie.photos)
      });
    },
    methods:{
      goBack(){
      this.$router.go(-1);
    }
    }
}
</script>
